.controlList {
  display: flex;
  margin-left: 50px;
}

.controlItem {
  cursor: pointer;
  &:hover {
    font-weight: 500;
  }
  &:not(:last-child) {
    margin-right: 10px;
  }
}

.container {
  display: flex;
  margin-right: 20px;
  align-items: center;
}

.controlItemActive {
  color: blue;
}
